.question-section {
  width: 100%;
  position: relative;
}



body {
  background-color: #7cc6fe;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.app {
  
  background-color: #9d9fa3;
  width: 450px;
  min-height: 200px;
  height: min-content;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 52px 10px rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: space-evenly;
  font-family: "Verdana", cursive, sans-serif;
  color: #ffffff;

}

.score-section {
  display: flex;
  font-size: 24px;
  align-items: center;
}

/* QUESTION/TIMER/LEFT SECTION */
.question-section {
  width: 100%;
  position: relative;
}

.question-count {
  margin-bottom: 20px;
}

.question-count span {
  font-size: 28px;
}

.question-text {
  margin-bottom: 12px;
}

.timer-text {
  background: rgb(230, 153, 12);
  padding: 15px;
  margin-top: 20px;
  margin-right: 20px;
  border: 5px solid rgb(255, 189, 67);
  border-radius: 15px;
  text-align: center;
}

/* ANSWERS/RIGHT SECTION */
.answer-section {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
}

button {
  width: 100%;
  font-size: 16px;
  color: #ffffff;
  background-color: #9d9fa3;
  border-radius: 15px;
  display: flex;
  padding: 5px;
  justify-content: flex-start;
  align-items: center;
  border: 5px solid #b3b3b3;
  cursor: pointer;
  
}



.btn:hover {
  background-color: #8bbbbb;
}

.btn:focus {
  outline: none;
}

.btn svg {
  margin-right: 5px;
}
